import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import tiujian from '@/components/tiujian'
import shipin from '@/components/shipin'
import redian from '@/components/redian'
import shehui from '@/components/shehui'
import yule from '@/components/yule'
import junshi from '@/components/junshi'
import ss from '@/components/ss'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/tiujian',
//    name: 'Hello',
      component: tiujian
    },
    {
      path: '/shipin',
//    name: 'Hello',
      component: shipin
    },
    {
      path: '/redian',
//    name: 'Hello',
      component: redian
    },
    {
      path: '/shehui',
//    name: 'Hello',
      component: shehui
    },
    {
      path: '/yule',
//    name: 'Hello',
      component: yule
    },
    {
      path: '/junshi',
//    name: 'Hello',
      component: junshi
    },
    {
      path: '/ss',
//    name: 'Hello',
      component: junshi
    }
  ]
})



